.wrapper {
  max-width: 1200px;
  width: 75%;
  margin-left: auto;
  margin-right: auto;
}

.flex-container {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.box {
  width: 100vw;
  height: 40vh;
  margin-bottom: 10px;
  min-width: 100%;
  background-color: white;
  border-radius: 5px;
  margin-top: 5px;
}

@media (min-width: 440px) {

.box {
    width: 100vw;
    height: 40vh;
    margin-bottom: 10px;
    margin-top: 10px;
    min-width: 100%;
    background-color: white;
    border-radius: 5px
}
  }

@media (min-width: 856px) {

.box {
    width: 40vw;
    height: 80vh;
    margin-bottom: 10px;
    min-width: 400px;
    background-color: white;
    border-radius: 5px;
    margin-top: 160px
}
  }

.box--top-margin {
    margin-top: 160px;
  }

@media (min-width: 440px) {

.box--top-margin {
      margin-top: 210px
  }
  }

@media (min-width: 856px) {

.box--top-margin {
      margin-top: 160px
  }
  }

.box__header {
    background-color: blue;
    color: white;
    border-radius: 5px 5px 0px 0px;
    font-size: 1.2em;
    padding: 10px 15px;
  }

.box__body {
    height: 89%;
    width: 100%;
    max-width: 550px;
    overflow: auto;
  }

@media (min-width: 856px) {

.box__body {
      width: 90%;
      height: 96%;
      overflow-wrap: break-word
  }
  }

.box__text-area {
    width: 96%;
    height: 90%;
    border: none;
    margin: 10px 4px;
  }

.box code {
    background-color: #d4d4d4;
  }

.App {
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #d4d4d4;
}

.header {
  width: 100%;
  background-color: white;
  position: fixed;
  left: 0;
  top: 0;
}

.header__title {
    font-size: 2em;
    text-align: center;
    padding: 10px 0px;
  }

@media (min-width: 440px) {

.header__title {
      font-size: 3em;
      text-align: center;
      padding: 10px 0px
  }
  }

.header__title-span {
    color: blue;
  }
